.Feed {
    margin-left: 250px;
}

.Feed-entry-list {
    padding-top: 0%;
    margin-bottom: 400px;
}
.feed-article-card {
    position: relative;
    background-color: #1e1e1e;
    border: 1px solid #333;
    border-radius: 8px;
    padding: 20px;
    margin: 10px;
    width: 70%;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s;
    flex-shrink: 0;
    text-align: start;
    padding-bottom: 45px;
}

.feed-article-card:hover {
    transform: scale(1.05);
}

.ciclecheck {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px;
    color: gray;
}
